$bg: #e7e7e7;
$text: #222;

html {
  background: $bg;
}

.Container {
  display: grid;
  grid-template-columns: 250px auto;
  height: 100vh;

  .Sidebar {
    border: 1px solid black;
    overflow-y: auto;
    overflow-x: hidden;

    .SidebarItem {
      display: flex;
      flex-direction: row;
      color: $text;
      cursor: pointer;
      
      .SidebarItemImage {
        width: 50px;
      }

      &.Active {
        background: black;
        color: white;
      }
    }
  }
  
  .Content {
    display: inline-flex;
    flex-direction: column;
    min-width: 100%;
    overflow: hidden;

    .ChartContainer {
      flex-basis: 0px;
      flex-grow: 1;
      max-height: 100%;
      margin: 50px;

      
    }

    .ItemInfo {
      display: grid;
      grid-template-areas: "pic name"
                           "pic stats";
      grid-template-rows: auto 1fr;
      grid-template-columns: auto 1fr;

      
      .LargeItemImage {
        width: 250px;
        height: 250px;
        grid-area: pic;
      }
  
      .LargeItemName {
        font-size: 28px;
        grid-area: name;
        color: $text;
      }

      .Statistics {
        grid-area: stats;
        font-size: 18px;
        color: $text;

      }
    }

     
  }
}

